class JoystickComp extends Component {
    render() {
        return (
            <div className="Button" >
                {this.props.edit ?
                    <div>
                        {this.props.type}
                    </div>
                    :
                    <ReactNipple
                        options={{
                            mode: 'static',
                            color: 'black',
                            restOpacity: 0.8,
                            position: { top: '50%', left: '50%' },
                            size: this.props.joystickSize
                        }}
                        style={{
                            outline: '1px',
                            width: this.props.joystickSize,
                            height: this.props.joystickSize
                        }}
                        onMove={(evt, data) => {
                            console.log(evt, data)
                            onJoystick(
                                this.props.type === "LeftJoyStick" ? "Left" :"Right",
                                data.distance * 200 / this.props.joystickSize,
                                data.angle.radian
                            );
                        }}
                        onTouchEnd={() => {
                            console.log('ended');
                            onJoystick(this.props.type === "LeftJoyStick" ? "Left" :"Right", 0, 0);
                        }}
                    />
                }
            </div>
        );
    }
}

const buttonMap = {
    A: AButton,
    B: BButton,
    X: XButton,
    Y: YButton,
    Up: UpButton,
    Down: DownButton,
    Left: LeftButton,
    Right: RightButton,
    Capture: CaptureButton,
    Home: HomeButton,
    Sync: SyncButton,
    Minus: MinusButton,
    Plus: PlusButton,
    L: LButton,
    R: RButton,
    ZL: ZLButton,
    ZR: ZRButton,
    SL: SLButton,
    SR: SRButton,
    LeftJoyStick: JoystickComp,
    RightJoyStick: JoystickComp
};

function onJoystick(dir, power, radians) {
    console.log(dir + ":" + power + ":" + radians);
    if (!window.joyconJS) {
        return;
    }
    radians +=  Math.PI / 2;
    if (dir === "Left") {
        window.joyconJS.onLeftJoystick(power, radians);
    } else if (dir === "Right") {
        window.joyconJS.onRightJoystick(power, radians);
    }
}
function onPress(name, pressed) {
    console.log(name + ":" + pressed);
    if (!window.joyconJS) {
        return;
    }
    switch (name) {
        case "Up":
            window.joyconJS.onUp(pressed);
            break;
        case "Down":
            window.joyconJS.onDown(pressed);
            break;
        case "Left":
            window.joyconJS.onLeft(pressed);
            break;
        case "Right":
            window.joyconJS.onRight(pressed);
            break;
        // case "SL":
        //     window.joyconJS.onLeftSL(pressed);
        //     break;
        // case "SR":
        //     window.joyconJS.onLeftSR(pressed);
        //     break;
        case "Capture":
            window.joyconJS.onCapture(pressed);
            break;
        case "L":
            window.joyconJS.onL(pressed);
            break;
        case "ZL":
            window.joyconJS.onZL(pressed);
            break;
        case "Minus":
            window.joyconJS.onMinus(pressed);
            break;
        case "A":
            window.joyconJS.onA(pressed);
            break;
        case "B":
            window.joyconJS.onB(pressed);
            break;
        case "X":
            window.joyconJS.onX(pressed);
            break;
        case "Y":
            window.joyconJS.onY(pressed);
            break;
        case "SL":
            window.joyconJS.onRightSL(pressed);
            break;
        case "SR":
            window.joyconJS.onRightSR(pressed);
            break;
        case "Home":
            window.joyconJS.onHome(pressed);
            break;
        case "Sync":
            window.joyconJS.onSync(pressed);
            break;
        case "R":
            window.joyconJS.onR(pressed);
            break;
        case "ZR":
            window.joyconJS.onZR(pressed);
            break;
        case "Plus":
            window.joyconJS.onPlus(pressed);
            break;
        default:
            break;
    }
}
class GamepadButton extends Component {

    constructor(props) {
        super(props);
        this.state = { pressed: false };
        this.onButtonEvent = this.onButtonEvent.bind(this);
    }

    onButtonEvent(event, pressed) {
        if (this.props.edit) {
            return;
        }
        onPress(this.props.type, pressed);
        this.setState({ pressed: pressed });
        if (this.props.onButtonEvent) {
            this.props.onButtonEvent(event, pressed);
        }
    }

    render() {
        const ButtonComp = buttonMap[this.props.type];
        return (
            <ButtonComp
                onButtonEvent={this.onButtonEvent}
                pressed={this.state.pressed}
                {...this.props}
            />
        );
    }
}

export default GamepadButton;


