import React, { Component } from 'react';
import { Rnd } from 'react-rnd';
import GamepadButton from './GamepadButton';
import './App.css';
import ButtonSelector, { buttons } from './ButtonSelector';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import IconButton from '@material-ui/core/IconButton';
import SaveIcon from '@material-ui/icons/Save';
import LoadIcon from '@material-ui/icons/GetApp';
import CloseIcon from '@material-ui/icons/Close';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Snackbar from '@material-ui/core/Snackbar';

const style = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    border: "solid 1px #ddd",
    background: "#f0f0f0"
};

const noneditableStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
};

function defaultDimensions(name) {
    var x = 0;
    var y = 0;
    var width = 100;
    var height = 100;
    switch (name) {
        case 'L':
        case 'R':
        case 'ZL':
        case 'ZR':
            width = 50;
            height = 200;
            break;
        case 'SL':
        case 'SR':
            width = 51;
            height = 102;
            break;
        default:
            break;
    }
    return { x, y, width, height };
}

class AdjustableUI extends Component {

    constructor(props) {
        super(props);
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
        this.onButtonsChanged = this.onButtonsChanged.bind(this);
        this.buttonNameToComponent = this.buttonNameToComponent.bind(this);
        this.editableChanged = this.editableChanged.bind(this);
        this.snapChanged = this.snapChanged.bind(this);
        this.getDimensions = this.getDimensions.bind(this);
        this.onResizeComponent = this.onResizeComponent.bind(this);
        this.onDragComponent = this.onDragComponent.bind(this);
        this.getButtonsFromLocalStorage = this.getButtonsFromLocalStorage.bind(this);
        this.saveDimensions = this.saveDimensions.bind(this);
        this.openSaveMenu = this.openSaveMenu.bind(this);
        this.closeSaveMenu = this.closeSaveMenu.bind(this);
        this.saveSlot = this.saveSlot.bind(this);
        this.openLoadMenu = this.openLoadMenu.bind(this);
        this.closeLoadMenu = this.closeLoadMenu.bind(this);
        this.loadSlot = this.loadSlot.bind(this);
        this.handleCloseSnackBar = this.handleCloseSnackBar.bind(this);
        const s = this.getButtonsFromLocalStorage();
        this.state = s;
    }

    getButtonsFromLocalStorage() {
        var state = { width: 0, height: 0, buttons: [], edit: false, snap: false, rightJoystickSize: 100, leftJoystickSize:100,
            saveMenuOpen: false, loadMenuOpen: false, snackOpen: false, snackMessage: ""};
        const joystickStateStr = window.localStorage.getItem('proControllerState');
        if (joystickStateStr) {
            var joystickState = JSON.parse(joystickStateStr);
            if (joystickState['RightJoyStick']) {
                state.rightJoystickSize = joystickState['RightJoyStick'].width;
            }
            if (joystickState['LeftJoyStick']) {
                state.leftJoystickSize = joystickState['LeftJoyStick'].width;
            }
            state.buttons = Object.keys(joystickState).filter(
                (buttonName) => buttons.includes(buttonName)
            );
        }

        return state;
    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    onButtonsChanged(changedButtons) {
        const joystickStateStr = window.localStorage.getItem('proControllerState');
        var joystickState = {};
        if (joystickStateStr) {
            joystickState = JSON.parse(joystickStateStr);
        }
        buttons.filter(
            b => !changedButtons.includes(b)
        ).map(b => delete joystickState[b]);
        window.localStorage.setItem('proControllerState', JSON.stringify(joystickState));

        this.setState({ buttons: changedButtons })
    }

    getDimensions(name) {
        const joystickStateStr = window.localStorage.getItem('proControllerState');
        var joystickState = {};
        if (joystickStateStr) {
            joystickState = JSON.parse(joystickStateStr);
        }

        var posDimen = joystickState[name];
        if (posDimen) {
            return posDimen;
        } else {
            return defaultDimensions(name);
        }
    }

    saveDimensions(name, dimen) {
        var joystickState = {};
        const joystickStateStr = window.localStorage.getItem('proControllerState');
        if (joystickStateStr) {
            joystickState = JSON.parse(joystickStateStr);
        }
        joystickState[name] = dimen;
        window.localStorage.setItem('proControllerState', JSON.stringify(joystickState));
    }

    onResizeComponent(name, e, direction, ref, delta, position) {
        const width = ref.offsetWidth;
        const height = ref.offsetHeight;
        const x = position.x;
        const y = position.y;
        if (name === 'RightJoyStick') {
            this.setState({ rightJoystickSize: width });
        }
        if (name === 'LeftJoyStick') {
            this.setState({ leftJoystickSize: width });
        }

        this.saveDimensions(name, { width, height, x, y });
    }

    onDragComponent(name, e, data) {
        var dimen = this.getDimensions(name);
        console.log(data.x + ',' + data.y);
        dimen.x = data.x;
        dimen.y = data.y;
        this.saveDimensions(name, dimen);
    }

    openSaveMenu() {
        this.setState({saveMenuOpen: true});
    }

    closeSaveMenu() {
        this.setState({saveMenuOpen: false});
    }

    saveSlot(e, i) {
        console.log('Save Slot '+i);
        var joystickState = {};
        const joystickStateStr = window.localStorage.getItem('proControllerState');
        if (joystickStateStr) {
            joystickState = JSON.parse(joystickStateStr);
        }
        window.localStorage.setItem('proControllerJoyConSlot'+i, JSON.stringify(joystickState));
        this.closeSaveMenu();
        this.setState({snackMessage: "Saved Slot "+i, snackOpen:true});
    }

    openLoadMenu() {
        this.setState({loadMenuOpen: true});
    }

    closeLoadMenu() {
        this.setState({loadMenuOpen: false});
    }

    loadSlot(e, i) {
        console.log('Load Slot '+i);
        const joystickStateStr = window.localStorage.getItem('proControllerJoyConSlot'+i);
        var joystickState = {};
        if (joystickStateStr) {
            joystickState = JSON.parse(joystickStateStr);
        }
        console.log(JSON.stringify(joystickState));
        window.localStorage.setItem('proControllerState', JSON.stringify(joystickState));
        this.closeLoadMenu();
        this.setState(this.getButtonsFromLocalStorage());
        this.setState({snackMessage: "Loaded Slot "+i, snackOpen:true});
    }

    buttonNameToComponent(name) {
        const posDimen = this.getDimensions(name);
        return (
            <Rnd
                key={name}
                style={this.state.edit ? style : noneditableStyle}
                resizeHandleClasses={{ bottomRight: "ResizerClass" }}
                disableDragging={!this.state.edit}
                enableResizing={{ top: false, right: false, bottom: false, left: false, topRight: false, bottomRight: this.state.edit, bottomLeft: false, topLeft: false }}
                lockAspectRatio={true}
                default={posDimen}
                resizeGrid={this.state.snap ? [10, 10] : [1, 1]}
                dragGrid={this.state.snap ? [10, 10] : [1, 1]}
                onResizeStop={(...params) => this.onResizeComponent(name, ...params)}
                onDragStop={(...params) => this.onDragComponent(name, ...params)}
            >
                {
                    name === 'RightJoyStick' ?
                        <GamepadButton type={name} edit={this.state.edit} joystickSize={this.state.rightJoystickSize} />
                        :
                    name === 'LeftJoyStick' ?
                        <GamepadButton type={name} edit={this.state.edit} joystickSize={this.state.leftJoystickSize} />
                        :
                        <GamepadButton
                            type={name}
                            edit={this.state.edit}
                        />
                }
            </Rnd>
        );
    }

    handleCloseSnackBar() {
        this.setState({snackOpen: false, snackMessage: ""});
    }

    snapChanged(event) {
        console.log(event.target.checked);
        this.setState({ snap: event.target.checked });
    }
    editableChanged(event) {
        console.log(event.target.checked);
        this.setState({ edit: event.target.checked });
    }

    render() {
        const buttons = this.state.buttons.map(
            this.buttonNameToComponent
        );
        return (
            <div className="AdjustableUI">
                <Snackbar
                    anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                    }}
                    open={this.state.snackOpen}
                    autoHideDuration={6000}
                    onClose={this.handleCloseSnackBar}
                    ContentProps={{
                    'aria-describedby': 'message-id',
                    }}
                    message={<span id="message-id">{this.state.snackMessage}</span>}
                    action={[
                    <IconButton
                        key="close"
                        aria-label="close"
                        color="inherit"
                        onClick={this.handleCloseSnackBar}
                    >
                        <CloseIcon />
                    </IconButton>,
                    ]}
                />
                <div className="Edit">
                    <FormGroup row>
                        {this.state.edit ?
                            <div>
                                <IconButton aria-label="Save" onClick={this.openSaveMenu} ref={x => this.saveAnchor = x}>
                                    <SaveIcon />
                                </IconButton>
                                <Menu
                                    id="simple-menu"
                                    anchorEl={this.saveAnchor}
                                    keepMounted
                                    open={this.state.saveMenuOpen}
                                    onClose={this.closeSaveMenu}
                                >
                                    {[...Array(10)].map((x, i) =>
                                        <MenuItem key={i} onClick={(e) => this.saveSlot(e, i)}>
                                            {'Save Slot ' + i}
                                        </MenuItem>
                                    )}
                                </Menu>
                            </div>
                            : null
                        }
                        {this.state.edit ?
                            <div>
                                <IconButton aria-label="Load" onClick={this.openLoadMenu} ref={x => this.loadAnchor = x}>
                                    <LoadIcon />
                                </IconButton>
                                <Menu
                                    id="simple-menu"
                                    anchorEl={this.loadAnchor}
                                    keepMounted
                                    open={this.state.loadMenuOpen}
                                    onClose={this.closeLoadMenu}
                                >
                                    {[...Array(10)].map((x, i) =>
                                        <MenuItem key={i} onClick={(e) => this.loadSlot(e, i)}>
                                            {'Load Slot ' + i}
                                        </MenuItem>
                                    )}
                                </Menu>
                            </div> : null
                        }
                        {this.state.edit ?
                            <FormControlLabel
                                control={
                                    <Switch
                                        checked={this.state.snap}
                                        onChange={this.snapChanged}
                                        value="editable"
                                        color="primary"
                                    />
                                }
                                label="Snap"
                            /> : null
                        }
                        <FormControlLabel
                            control={
                                <Switch
                                    checked={this.state.edit}
                                    onChange={this.editableChanged}
                                    value="editable"
                                    color="primary"
                                />
                            }
                            label="Edit"
                        />
                    </FormGroup>
                </div>
                {this.state.edit ?
                    <div className="ButtonPicker">
                        <ButtonSelector
                            buttons={this.state.buttons}
                            onButtonsChanged={this.onButtonsChanged}
                        />
                    </div> : null
                }
                {buttons}
            </div>
        );
    }
}

export default AdjustableUI;


