import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Chip from '@material-ui/core/Chip';

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    chips: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    chip: {
        margin: 2,
    },
    noLabel: {
        marginTop: theme.spacing(3),
    },
}));

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};

export const buttons = [
    'X',
    'Y',
    'A',
    'B',
    'Up',
    'Down',
    'Right',
    'Left',
    'Capture',
    'Home',
    'Sync',
    'Minus',
    'Plus',
    'L',
    'R',
    'ZL',
    'ZR',
    //'SL',
    //'SR',
    'LeftJoyStick',
    'RightJoyStick'
];


export default function ButtonSelector(props) {
    const classes = useStyles();
    const [buttonNames, setButtonNames] = React.useState(props.buttons);

    function handleChange(event) {
        setButtonNames(event.target.value);
        if (props.onButtonsChanged) {
            props.onButtonsChanged(event.target.value);
        }
    }

    return (
        <div className={classes.root}>
            <FormControl className={classes.formControl}>
                <InputLabel htmlFor="select-multiple-chip">Buttons</InputLabel>
                <Select
                    multiple
                    value={buttonNames}
                    onChange={handleChange}
                    input={<Input id="select-multiple-chip" />}
                    renderValue={selected => (
                        <div className={classes.chips}>
                            {selected.map(value => (
                                <Chip key={value} label={value} className={classes.chip} />
                            ))}
                        </div>
                    )}
                    MenuProps={MenuProps}
                >
                    {buttons.map(button => (
                        <MenuItem key={button} value={button}>
                            {button}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>
        </div>
    );
}


